@font-face{font-family:"Ownglyph_jooreeletter-Rg";src:url("../../Font/온글잎 주리손편지.ttf")}.loginPage{width:420px;background-color:#fff;box-shadow:0 20px 50px rgba(0,0,0,.1);border-radius:12px;overflow:hidden;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.loginPage::before{content:"";position:absolute;background:rgba(0,0,0,0);bottom:45px;right:40px;width:55px;height:55px;z-index:5;transition:all .5s ease-in-out,background 0s ease}.loginPage .form{display:block;position:relative}.loginPage .form h2{background-color:#fdde55;display:block;box-sizing:border-box;width:100%;margin:0 0 30px 0;padding:40px;font-weight:bold;color:#151515;font-size:19px}.loginPage .form h2 span{font-family:"Ownglyph_jooreeletter-Rg";font-size:25px}.loginPage .form .form-field{display:flex;align-items:center;height:55px;margin:0 40px 30px 40px;border-bottom:1px solid #151515}.loginPage .form .form-field label{width:10px;padding:0 15px 0 0;color:#151515}.loginPage .form .form-field input{width:100%;background-color:rgba(0,0,0,0);color:#151515;padding:15px;border:0;margin:0}.loginPage .form .form-field input+svg{width:35px;fill:none;stroke:#fdde55;stroke-width:7;stroke-linecap:round;stroke-dasharray:1000;stroke-dashoffset:-100;transition:all .3s ease-in-out}.loginPage .form .form-field input:valid+svg{stroke-dashoffset:0}.loginPage .form .form-field input:focus{outline:none}.loginPage .form .form-field *::placeholder{color:#b4b4b8}.loginPage .form .button{width:100%;position:relative;cursor:pointer;box-sizing:border-box;padding:0 40px 45px 40px;margin:0;border:0;background:rgba(0,0,0,0);outline:none}.loginPage .form .button .arrow-wrapper{transition:all .45s ease-in-out;position:relative;margin:0;width:100%;height:55px;right:0;float:right;background:linear-gradient(90deg, #F1C93B, #FDDE55);box-shadow:0 3px 20px rgba(253,222,85,.4);border-radius:12px}.loginPage .form .button .arrow{position:absolute;top:50%;margin:auto;transition:all .4s ease-in-out;right:35px;width:15px;height:2px;background:none;transform:translateY(-50%)}.loginPage .form .button .arrow::before{position:absolute;content:"";top:-4px;right:0;width:8px;height:8px;border-top:2px solid #fff;border-right:2px solid #fff;transform:rotate(45deg)}.loginPage .form .button .button-text{transition:all .45s ease-in-out;position:absolute;top:0;left:0;right:0;padding:0;margin:0;color:#fff;line-height:55px;text-align:center;text-transform:uppercase;font-weight:bold}.loginPage .finished{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:7}.loginPage .finished svg{width:100px;fill:none;stroke:#fff;stroke-width:7;stroke-linecap:round;stroke-dasharray:1000;stroke-dashoffset:-100;transition:all .3s ease-in-out .5s}.loginPage.loading .form .button .arrow-wrapper{width:55px;animation:sk-rotateplane 1.2s infinite ease-in-out .5s}.loginPage.loading .form .button .arrow-wrapper .arrow{background-color:#fff;transform:translate(15px, 0);opacity:0;transition:opacity .3s ease-in-out .5s}.loginPage.loading .form .button .button-text{color:#f1c93b}.loginPage.active::before{bottom:0;right:0;background:linear-gradient(90deg, #F1C93B, #FDDE55);border-radius:12px;height:100%;width:100%}.loginPage.active .form .button .arrow-wrapper{animation-iteration-count:1}.loginPage.active .finished svg{stroke-dashoffset:0}@keyframes sk-rotateplane{0%{transform:perspective(120px)}50%{transform:perspective(120px) rotateY(180deg)}100%{transform:perspective(120px) rotateY(180deg) rotateX(180deg)}}@-webkit-keyframes sk-rotateplane{0%{transform:perspective(120px)}50%{transform:perspective(120px) rotateY(180deg)}100%{transform:perspective(120px) rotateY(180deg) rotateX(180deg)}}